import * as $ from 'jquery'
// import Scrollbar from '@ued/scrollbar'
import Select from '../../../../release/index'

import '@/sass/global.scss'
import './index.scss'

$(function(){
    $(function() {
		Select({
			target: $(".ue-select"),
			trigger: "click",
			currentClass: "cur",
			defaultClass: "ue-select",
			activeClass: "ue-select-active",
			selected: $(".ue-select-title span"),
			title: $(".ue-select-title"),
			list: $(".ue-select-list"),
			items: $(".ue-select-list li"),
			dividerClass: "divider",
			onSelected: function(target, index) {
				this.hide();
				return false;
			}
		});
	})
})




